import React from 'react'
import PropTypes from 'prop-types'
import { StaticQuery, graphql, Link } from 'gatsby'

import Header from './header'
import './layout.css'

const Layout = ({ children }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title,
            author
          }
        }
      }
    `}
    render={data => (
      <div className="site">
        <Header siteTitle={data.site.siteMetadata.title} />
        <div className="site-content">
          {children}
        </div>
        <footer>
          <div>
            <span className="label">Stay in touch</span>
            <a href="https://github.com/vivensn" target="_blank" className="social-media-icon-link"><span className="ion-logo-github"></span></a>
            <a href="https://twitter.com/vivens_n" target="_blank" className="social-media-icon-link"><span className="ion-logo-twitter"></span></a>
            <a href="https://www.linkedin.com/in/vivens-ndatinya-baa4a658/" target="_blank" className="social-media-icon-link"><span className="ion-logo-linkedin"></span></a>
          </div>
          <div>
            <span><a href>Coded with love by {data.site.siteMetadata.author} {new Date().getFullYear()}</a></span>
          </div>
        </footer>
      </div>
    )}
  />
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
